<script setup lang="ts">
import { LoginUser } from '@/commons/Global';
import { Setting } from '@element-plus/icons-vue';
import * as el from 'element-plus'
import BaseDialog from './BaseDialog.vue';
import { ref } from 'vue';
import { signin, signout } from '@/commons/oidc/handle';

const logouted = ref(false)
</script>

<template>
    <BaseDialog -body="退出登录成功" v-model="logouted"></BaseDialog>

    <div style="font-size: 13px;">
        <el.ElDropdown style="margin-right: 3px;">
            <el.ElIcon>
                <Setting />
            </el.ElIcon>
            <template #dropdown>
                <el.ElDropdownMenu>
                    <el.ElDropdownItem @click="signin()">登录</el.ElDropdownItem>
                    <el.ElDropdownItem @click="signout().then(b => logouted = b)">退出登录</el.ElDropdownItem>
                </el.ElDropdownMenu>
            </template>
        </el.ElDropdown>
        <a>{{ LoginUser?.profile.name ?? '未登录' }}</a>
    </div>
</template>