import { UserManager } from "oidc-client-ts";
// import * as oidc_client from 'oidc-client-ts'
import { settings } from "./define";
import { DeleteUserInfo, StorageUserInfo } from "../Global";

export const client = new UserManager(settings)

export async function signin() {
    await client.signinRedirect()
}

export async function signout():Promise<boolean> {
    if (await client.getUser() === null) {
        return false
    }
    await DeleteUserInfo()
    return true
}

export const enum CallbackStatus {
    OK,
    FAILURE
}

export async function signinCallback():Promise<CallbackStatus> {
    const result = await client.signinCallback()
    if (result === undefined) {
        return CallbackStatus.FAILURE
    }
    await StorageUserInfo(result)
    return CallbackStatus.OK
}