<script setup lang="ts">
import { ref, watchEffect } from 'vue';
const props = defineProps({
    Body: {
        type: String,
        required: true,
    },
    EnterAction: {
        type: Function,
    },
    CloseAction: {
        type: Function,
        default: function() {}
    },
    Cancel: {
        type: Boolean
    }
})
const visableStatus = defineModel({
    type: Boolean,
    required: true
})
if (props.CloseAction) {
    const visabled = ref(false)
    watchEffect(() => {
        if (visableStatus.value && !visabled.value) {
            visabled.value = true
        }
        if (!visableStatus.value && visabled.value) {
            props.CloseAction()
        }
    })
}
</script>

<template>
    <ElDialog title="提示" v-model="visableStatus">
        <span>{{ Body }}</span>
        <template #footer>
            <ElButton @click="(EnterAction) ? EnterAction() : visableStatus = false">确认</ElButton>
            <ElButton v-if="Cancel" @click="visableStatus = false">取消</ElButton>
        </template>
    </ElDialog>
</template>