import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import HomeView from '../views/HomeView.vue'
const RoomList = () => import("../views/RoomListView.vue")
const RoomDetail = () => import("../views/RoomDetailView.vue")
const UserLogin = () => import("../views/UserLoginView.vue")
const UserDetail = () => import("../views/UserDetailView.vue")
const UserLogout = () => import("../views/UserLogoutView.vue")
const RoomCreate = () => import("../views/RoomCreateView.vue")
const OIDCCallback = () => import("../views/OIDCCallbackView.vue")

const routes: RouteRecordRaw[] = [
  {
    path: '/',
    name: 'Home',
    component: HomeView
  },
  {
    path: '/rooms/create',
    name: "RoomCreate",
    component: RoomCreate
  },
  {
    path: '/rooms',
    name: 'RoomList',
    component: RoomList
  },
  {
    path: "/rooms/:id",
    name: "RoomDetail",
    component: RoomDetail
  },
  {
    path: "/user/login",
    name: "UserLogin",
    component: UserLogin
  },
  {
    path: "/user/logout",
    name: "UserLogout",
    component: UserLogout
  },
  {
    path: "/user/:id",
    name: "UserDetail",
    component: UserDetail,
    children: [

    ]
  },
  {
    path: "/callback",
    name: "OIDCCallback",
    component: OIDCCallback
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
