export function SetCookie(key: string, value: string, maxAge: number = 365*24*60*60, options?: {
    top: boolean
}) {
    let domain = location.hostname
    if (options?.top) {
        if (domain.split(".").length >= 1) {
            const hostArr = domain.split(".")
            hostArr.shift()
            domain = '.'+hostArr.join(".")
        }
    }
    document.cookie = `${key}=${value}; path=/; max-age=${maxAge};samesite=none; secure; domain=${domain}`
}

export function DelCookie(key: string) {
    // 不确定是否为top，删两次
    let domain = location.hostname
    if (domain.split(".").length >= 1) {
        const hostArr = domain.split(".")
        hostArr.shift()
        domain = '.'+hostArr.join(".")
    }
    document.cookie = `${key}=; expires=Thu, 01 Jan 1970 00:00:00 GMT`
    document.cookie = `${key}=; expires=Thu, 01 Jan 1970 00:00:00 GMT; domain=${domain}`
}

export function CutDomain(domain: string): string|undefined {
    return domain.match(`^(?:[a-z0-9-]+\\.)+([a-z0-9-]+\\.[a-z]{2,})$`)?.[0]
}

export function GetCookie(key: string) {
    const cs = document.cookie.split(";")
    for (let c of cs) {
        c = TrimSpace(c)
        const cSlice = c.split("=")
        const cKey = cSlice[0]
        const cValue = cSlice[1]
        if (cKey === key) {
            return cValue
        }
    }
    return undefined
}

