async function updateUserInfoFromOIDCClient() {
    const user = await client.getUser()
    if (user === null) {
        LoginUser.value = undefined
        DelCookie('access_token')
        return
    }
    // 判断是否需要更新cookie
    if (LoginUser.value === undefined || LoginUser.value.access_token !== user.access_token) {
        updateUserInfoToCookie(user)
    }
    LoginUser.value = user
}

function updateUserInfoToCookie(u: User) {
    const now = GetTimeSec()
    let dur:number|undefined
    if (u.expires_at !== undefined) {
        dur = u.expires_at - now
    }
    SetCookie("access_token", u.access_token, dur, {
        top: true
    })
}

export async function StorageUserInfo(u: User) {
    await client.storeUser(u)
    updateUserInfoFromOIDCClient()
}

export async function DeleteUserInfo() {
    await client.removeUser()
    updateUserInfoFromOIDCClient()
}

export const GetTimeSec = (d: Date = new Date()) => Math.floor(d.getTime()/1000)

